import React, { useState, useEffect } from 'react';
import { getAvailableTimes, makeReservation } from '../services/reservation';
import ReservationForm from '../components/ReservationForm';
import Sidebar from '../components/Sidebar';

const Agendamento: React.FC = () => {
  const [availableTimes, setAvailableTimes] = useState([]);
  const [error, setError] = useState('');
  const [environmentId, setEnvironmentId] = useState(0);

  useEffect(() => {
    const fetchAvailableTimes = async () => {
      try {
        const response = await getAvailableTimes(environmentId);
        setAvailableTimes(response.data);
      } catch (err) {
        setError('Erro ao carregar os horários disponíveis');
      }
    };

    if (environmentId) {
      fetchAvailableTimes();
    }
  }, [environmentId]);

  const handleReservation = async (data: { date: string; time: string }) => {
    try {
      await makeReservation(data);
      setError('');
      alert('Reserva realizada com sucesso');
    } catch (err) {
      setError('Erro ao realizar a reserva');
    }
  };

  return (
    <div style={{ display: 'flex' }}>
      <Sidebar />
      <div style={{ marginLeft: '250px', padding: '20px' }}>
        <h1>Agendar Reserva</h1>
        <select onChange={(e) => setEnvironmentId(Number(e.target.value))}>
          <option value="">Selecione um ambiente</option>
          <option value={1}>Biblioteca</option>
          <option value={2}>Salas</option>
          <option value={3}>AlphaLAB</option>
          <option value={4}>Auditório</option>
        </select>

        {error && <p style={{ color: 'red' }}>{error}</p>}

        {availableTimes.length > 0 ? (
          <ReservationForm availableTimes={availableTimes} onReserve={handleReservation} />
        ) : (
          <p>Selecione um ambiente para ver os horários disponíveis.</p>
        )}
      </div>
    </div>
  );
};

export default Agendamento;
